html {
  height: 100%;
}

body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  font-family: -apple-system, system-ui, Helvetica, Segoe, sans-serif;
  background: -webkit-gradient(
    linear,
    left bottom, left top,
    from(rgba(2, 0, 36, 1)),
    color-stop(35%, rgba(21, 22, 31, 1)),
    to(rgba(52, 55, 79, 1))
  );
  background: -webkit-linear-gradient(
    bottom,
    rgba(2, 0, 36, 1) 0%,
    rgba(21, 22, 31, 1) 35%,
    rgba(52, 55, 79, 1) 100%
  );
  background: -o-linear-gradient(
    bottom,
    rgba(2, 0, 36, 1) 0%,
    rgba(21, 22, 31, 1) 35%,
    rgba(52, 55, 79, 1) 100%
  );
  background: linear-gradient(
    0deg,
    rgba(2, 0, 36, 1) 0%,
    rgba(21, 22, 31, 1) 35%,
    rgba(52, 55, 79, 1) 100%
  );
  background-repeat: no-repeat;
  background-attachment: fixed;
}



